import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'
import { Formik, Form, Field } from 'formik'
import { TextField } from 'formik-material-ui'
import * as Yup from 'yup'
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Grid,
  makeStyles,
  useMediaQuery,
  useTheme,
} from '@material-ui/core'
import { Grade, ShoppingCart } from '@material-ui/icons'

import ActionButton from '../ActionButton'
import ListErrors from '../ListErrors'
import agent from '../../agent'

import {
  ADD_TRANSACTION,
  DELETE_TRANSACTION,
  UPDATE_TRANSACTION,
  TRANSACTION_DIALOG_UNLOADED,
} from '../../constants/actionTypes'

const useStyles = makeStyles((theme) => ({
  root: {
    // padding: theme.spacing(2),
  },
}))

const mapStateToProps = (state) => ({
  ...state.scores,
})

const mapDispatchToProps = (dispatch) => ({
  onAdd: (transaction) =>
    dispatch({
      type: ADD_TRANSACTION,
      payload: agent.Transactions.add(transaction),
      snackbar: {
        message: `${transaction.typeTitle} added`,
        variant: 'success',
      },
    }),
  onExit: () => dispatch({ type: TRANSACTION_DIALOG_UNLOADED }),
})

const TransactionDialog = ({
  errors,
  inProgress,
  onAdd,
  onExit,
  student,
  transaction,
  type,
}) => {
  const [open, setOpen] = useState(false)

  const theme = useTheme()
  const fullScreen = useMediaQuery(theme.breakpoints.down('md'))
  const classes = useStyles()

  const handleClickOpen = () => {
    setOpen(true)
  }

  const handleClose = () => {
    setOpen(false)
  }

  const handleSubmitAdd = (values) => {
    onAdd({ ...values, student, type: type.toLowerCase(), typeTitle: type })
  }

  const handleSubmitUpdate = (values) => {
    // onUpdate({ ...course, ...values })
  }

  useEffect(() => {
    // Close the dialog only if there are no errors
    if (!inProgress && !errors) setOpen(false)
  }, [inProgress])

  return (
    <>
      <ActionButton
        action={`Add ${type.toLowerCase()}`}
        onClick={handleClickOpen}
      >
        {type === 'Bonus' && <Grade />}
        {type === 'Purchase' && <ShoppingCart />}
      </ActionButton>

      <Dialog
        fullScreen={fullScreen}
        onClick={(ev) => ev.stopPropagation()}
        onClose={handleClose}
        onExit={onExit}
        onFocus={(ev) => ev.stopPropagation()}
        open={open}
      >
        <Formik
          initialValues={{
            amount: transaction?.amount || '',
            description: transaction?.description || '',
          }}
          validationSchema={Yup.object({
            amount: Yup.number().typeError('Numbers only').required('Required'),
            description: Yup.string().required('Required'),
          })}
          onSubmit={async (values) => {
            transaction ? handleSubmitUpdate(values) : handleSubmitAdd(values)
          }}
        >
          <Form>
            <DialogTitle>
              {`${transaction ? 'Edit' : 'Add'} ${type}`}
            </DialogTitle>
            <DialogContent>
              <ListErrors errors={errors} />
              <Grid container spacing={2}>
                <Grid item xs={12} md={6}>
                  <Field
                    autoFocus={transaction ? false : true}
                    color='primary'
                    component={TextField}
                    disabled={inProgress}
                    label='Description'
                    name='description'
                    type='text'
                    fullWidth
                  />
                </Grid>
                <Grid item xs={12} md={6}>
                  <Field
                    color='primary'
                    component={TextField}
                    disabled={inProgress}
                    label='Amount'
                    name='amount'
                    type='text'
                    fullWidth
                  />
                </Grid>
              </Grid>
            </DialogContent>
            <DialogActions>
              <Button
                disabled={inProgress}
                color='primary'
                type='submit'
                variant='contained'
              >
                {transaction
                  ? inProgress
                    ? 'Updating...'
                    : 'Update'
                  : inProgress
                  ? 'Adding...'
                  : 'Add'}
              </Button>
              <Button
                disabled={inProgress}
                onClick={handleClose}
                variant='contained'
              >
                Cancel
              </Button>
            </DialogActions>
          </Form>
        </Formik>
      </Dialog>
    </>
  )
}

export default connect(mapStateToProps, mapDispatchToProps)(TransactionDialog)
