// @todo: do score->points conversion here?
const convertScore = (score, conversions) => {
  const total = score.task.totalPoints
  const noZeroTotal = total !== 0 ? total : 1
  return conversions[
    Math.floor(Math.round((score.value / noZeroTotal) * 100) / 10) * 10
  ]
}

export default convertScore
