import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux'
import {
  Checkbox,
  Grid,
  FormControlLabel,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Tooltip,
  Typography,
  makeStyles,
} from '@material-ui/core'
import { InfoOutlined } from '@material-ui/icons'

import PageHeader from '../PageHeader'
import SongRequestDialog from './SongRequestDialog'
import agent from '../../agent'

import {
  STUDENT_SONGS_PAGE_LOADED,
  SONGS_PAGE_UNLOADED,
} from '../../constants/actionTypes'

const useStyles = makeStyles((theme) => ({
  icon: {
    marginLeft: theme.spacing(1),
  },
  paper: {
    paddingLeft: theme.spacing(2),
    marginBottom: theme.spacing(4),
  },
  status: {
    alignItems: 'center',
    display: 'flex',
  },
}))

const mapStateToProps = (state) => ({
  ...state.songs,
  currentUser: state.common.currentUser,
})

const mapDispatchToProps = (dispatch) => ({
  onLoad: (payload) => dispatch({ type: STUDENT_SONGS_PAGE_LOADED, payload }),
  onUnload: () => dispatch({ type: SONGS_PAGE_UNLOADED }),
})

const DataRow = ({ song, showStudent }) => {
  const classes = useStyles()

  return (
    <TableRow>
      <TableCell>{new Date(song.createdAt).toDateString()}</TableCell>
      <TableCell>{song.title}</TableCell>
      <TableCell>{song.artist}</TableCell>
      {showStudent && (
        <TableCell>
          {song.student.firstName} {song.student.lastName}
        </TableCell>
      )}
      <TableCell>
        <span className={classes.status}>
          {song.statusDescription ? (
            <>
              {song.status}
              <Tooltip
                arrow
                color='primary'
                enterTouchDelay={0}
                title={song.statusDescription}
              >
                <InfoOutlined className={classes.icon} />
              </Tooltip>
            </>
          ) : (
            song.status
          )}
        </span>
      </TableCell>
    </TableRow>
  )
}

const CourseSongs = ({ songs }) =>
  songs.length > 0 ? (
    songs.map((song) => (
      <DataRow key={song._id} song={song} showStudent={true} />
    ))
  ) : (
    <TableRow>
      <TableCell colSpan='5'>
        <Typography align='center'>
          No songs have been requested for your period
        </Typography>
      </TableCell>
    </TableRow>
  )

const UserSongs = ({ songs }) =>
  songs.length > 0 ? (
    songs.map((song) => (
      <DataRow key={song._id} song={song} showStudent={false} />
    ))
  ) : (
    <TableRow>
      <TableCell colSpan='4'>
        <Typography align='center'>You have not requested any songs</Typography>
      </TableCell>
    </TableRow>
  )

const StudentSongs = ({
  course,
  courseSongs,
  currentUser,
  onLoad,
  onUnload,
  userSongs,
}) => {
  const [mySongsOnly, setMySongsOnly] = useState(true)

  const classes = useStyles()

  const handleMySongsOnlyClick = (event) => {
    setMySongsOnly(event.target.checked)
  }

  useEffect(() => {
    onLoad(agent.Songs.forStudent())

    return () => {
      return onUnload()
    }
  }, [])

  return (
    <>
      <PageHeader
        title={
          'Songs for ' +
          (mySongsOnly ? currentUser.firstName : 'Period ' + course.period)
        }
      >
        <SongRequestDialog />
      </PageHeader>

      <Paper className={classes.paper}>
        <Grid container spacing={2} className={classes.grid}>
          <Grid item xs={12} className={classes.grid}>
            <FormControlLabel
              control={
                <Checkbox
                  checked={mySongsOnly}
                  onChange={handleMySongsOnlyClick}
                  name='mySongsOnly'
                />
              }
              label='show only my songs'
            />
          </Grid>
        </Grid>
      </Paper>

      <TableContainer component={Paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>Request Date</TableCell>
              <TableCell>Song Title</TableCell>
              <TableCell>Song Artist</TableCell>
              {!mySongsOnly && <TableCell>Purchased by</TableCell>}
              <TableCell>Status</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {mySongsOnly ? (
              <UserSongs songs={userSongs} />
            ) : (
              <CourseSongs songs={courseSongs} />
            )}
          </TableBody>
        </Table>
      </TableContainer>
    </>
  )
}

export default connect(mapStateToProps, mapDispatchToProps)(StudentSongs)
